
import { PAGE_SIZE } from "@/shared/constants/common.const";
import Pagination from "@/shared/components/Pagination/Pagination.vue";
import VueDatePicker from '@vuepic/vue-datepicker';
import '@vuepic/vue-datepicker/dist/main.css'
import { defineComponent } from "vue";
import { RPT205000711_REQ, RPT205000711_RES, SCHEDULE_PATMENT_SERVICE } from "@/shared/type/RPT205000711.type";
export default defineComponent({
    name: 'RPT202001SS06',

    components: { 
        Pagination, 
        VueDatePicker 
    },

    data() {
        return {
            approvalRequestDate: "" as unknown,
            header: [
                { columnName: 'No' },
                { columnName: 'Reference No' },
                { columnName: 'Biz CID' },
                { columnName: 'Biz Name' },
                { columnName: 'Transaction Type' },
                { columnName: 'From Account No' },
                { columnName: 'To Account No' },
                { columnName: 'To Account Name' },
                { columnName: 'Service Provider' },
                { columnName: 'Phone Number' },
                { columnName: 'Bank Name' },
                { columnName: 'Amount' },
                { columnName: 'Currency' },
                { columnName: 'Fee Charge' },
                { columnName: 'Schedule Type' },
                { columnName: 'Effective Date' },
                { columnName: 'Remark' },
                { columnName: 'Status' },
                { columnName: 'Created by' },
                { columnName: 'Created date' },
                { columnName: 'Authorized by' },
                { columnName: 'Authorized Date' },
            ],
            table: [] as SCHEDULE_PATMENT_SERVICE[],
            totalItems: 0,
            currentPage: 1,
            pageSize: PAGE_SIZE,
            startingIndex: 1,
            isResetPage: false,
        }
    },
    
    mounted() {
        this.requestSchedulePaymentDetail()
    },

    computed: {
        formattedDate(): string {
            return this.approvalRequestDate ? this.$datetime.formatVueDatePicker(this.approvalRequestDate as Date) : ""
        },
    },

    methods: {
        async requestSchedulePaymentDetail() {
            const reqBody: RPT205000711_REQ = {
                approvalRequestDate: this.approvalRequestDate ? this.$datetime.convertDateToString(this.approvalRequestDate as Date) : '',
                pageSize: this.pageSize,
                pageNumber: this.currentPage,
            }
            const res = await this.$request.getRequest("RPT20500711", reqBody, false) as RPT205000711_RES;
            this.table = res.body.list.map((item: SCHEDULE_PATMENT_SERVICE) => {
                return {
                    serverTransactionID: item.serverTransactionID,
                    customerNo: item.customerNo,
                    customerName: item.customerName,
                    eBankTransactionTypeCode: item.eBankTransactionTypeCode,
                    withdrawalAccountNo: item.withdrawalAccountNo,
                    receiverAccountNo: item.receiverAccountNo,
                    receiverName: item.receiverName,
                    mobileOperatorName: item.mobileOperatorName,
                    cellPhoneNo: item.cellPhoneNo,
                    bankName: item.bankName,
                    amount: item.amount,
                    currencyCode: item.currencyCode,
                    commissionFee: item.commissionFee,
                    transactionFrequencyCode: item.transactionFrequencyCode,
                    scheduleStartDate: item.scheduleStartDate,
                    remark: item.remark,
                    corpBankingApprovalStatusCode: item.corpBankingApprovalStatusCode,
                    firstRegisterTellerID: item.firstRegisterTellerID,
                    firstRegisterDate: item.firstRegisterDate,
                    approverID: item.approverID,
                    authorizedDate: item.authorizedDate
                }
            })
            this.totalItems = res.body.totalCount
        },

        handlePagination(pageNumber: number) {
            this.isResetPage = false;
            this.currentPage = pageNumber;
            this.requestSchedulePaymentDetail();
        },

        handleStartingIndex(startingIndex: number) {
            this.startingIndex = startingIndex;
        },

        handleSearch() {
            if (this.currentPage > 1) {
                this.currentPage = 1
                this.isResetPage = true;
                this.requestSchedulePaymentDetail()
            } else {
                this.isResetPage = true;
                this.requestSchedulePaymentDetail()
            }
        },

        handleReset() {
            this.approvalRequestDate = "" as unknown
            this.currentPage = 1
            this.isResetPage = true
            this.requestSchedulePaymentDetail()
        }
    }
})
